@use "base/variables" as v;
@use "base/mixins" as m;

.home {
  .hero-img {
    position: relative;
    .hero-text {
      width: 100%;
      position: absolute;
      bottom: 1.5rem;
      font-family: v.$subtitle;
      @include m.mobile {
        bottom: 4rem;
      }
      @include m.laptop {
        bottom: 6rem;
      }
      @include m.desktop {
        bottom: 5rem;
      }
      @include m.desktopXL {
        bottom: 8rem;
      }
      h1 {
        width: 100%;
        text-align: center;
        color: v.$white;
        font-family: v.$text;
        @include m.laptop {
          font-size: 3rem;
        }
        @include m.desktop {
          font-size: 4rem;
        }
      }
      .hero-btn {
        display: none;
        @include m.tablet {
          display: block;
        }
      }
      @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        h1 {
          span {
            display: block;
            font-family: v.$title;
          }
        }
        .hero-btn {
          display: flex;
          justify-content: space-around;
          align-items: center;
          color: v.$white;
          font-size: 1.25rem;
          margin: 2rem;
          a {
            padding: 1rem;
            border: 2px solid v.$white;
            border-radius: 2rem;
            font-family: v.$text;
            transition: all 0.5s;
            &:hover {
              color: v.$white;
              background-color: lighten($color: v.$primaryPinkDark, $amount: 5);
              transition: all 0.5s;
            }
            @include m.laptop {
              font-size: 2rem;
            }
            @include m.desktop {
              font-size: 3rem;
              padding: 1rem 3rem;
            }
          }
        }
      }
    }
  }
}

.home-title {
  h2 {
    font-family: v.$text;
    margin: 4rem;
    margin-top: 2rem;
    text-align: center;
    color: v.$primaryPinkDark;
    font-family: v.$text;
    @include m.mobile {
      margin: 4rem 3rem;
      margin-top: 2rem;
    }
    @include m.desktop {
      margin: 10rem 3rem 5rem 3rem;
      margin-top: 4rem;
    }
    @include m.desktopXL {
      margin-top: 8rem;
    }
    span {
      font-family: v.$title;
    }
  }
  p {
    text-align: center;
    margin: 3rem;
    color: v.$secondaryGray;
    @include m.tablet {
      margin: 3rem 10rem 7rem;
    }
    @include m.laptop {
      margin: 3rem 15rem 7rem;
    }
    @include m.desktopXL {
      margin: 3rem 35rem 15rem;
    }
  }
}

.home-Grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(6, 1fr);
  margin: 2rem;
  gap: 2rem;
  .grid-element-odd {
    background-color: v.$primaryPinkDark;
    &:hover {
      background-color: darken($color: v.$primaryPinkDark, $amount: 5);
    }
  }
  .grid-element-pair {
    background-color: v.$primaryPink;
    &:hover {
      background-color: darken($color: v.$primaryPink, $amount: 5);
    }
  }
  .grid-img {
    display: flex;
    justify-content: center;
    height: 13rem;
    img {
      height: 100%;
      padding: 3rem 0;
      object-fit: contain;
    }
    @include m.tablet {
      height: 12rem;
    }
    @include m.laptop {
      height: 15rem;
    }
    @include m.desktop {
      height: 18rem;
    }
  }
  .grid-text {
    color: v.$white;
    text-align: center;
    padding-bottom: 3rem;
    font-size: 1.25rem;
    font-family: v.$text;
    @include m.mobile {
      padding: 1rem 1rem 2rem 1rem;
      font-size: 1.5rem;
    }
    @include m.tablet {
      font-size: 1.75rem;
    }
    @include m.desktop {
      font-size: 2.2rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
      padding: 2rem 2rem 4rem 2rem;
    }
  }
  @include m.mobile {
    margin: 2rem 20%;
  }
  @include m.tablet {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    margin: -2rem 2rem 5rem 2rem;
    gap: 2rem;
  }
  @include m.laptop {
    margin: -2rem 10rem 10rem 10rem;
    gap: 3rem;
  }
  @include m.desktop {
    max-width: 90rem;
    margin: 10rem auto 10rem auto;
    gap: 3rem;
  }
  @include m.desktopXL {
    max-width: 110rem;
  }
}

.directory-link {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: v.$primaryPinkDark;
  text-align: center;
  color: v.$white;
  padding: 2rem 1rem;
  span {
    display: block;
  }
  transition: all 0.5s;
  &:hover {
    // background-color: darken($color: v.$primaryPinkDark, $amount: 10);
    color: v.$white;
    transform: scale(1.05);
    transition: all 0.5s;
  }
  p {
    width: 100%;
    margin: 0;
  }
  @include m.mobile {
    border-radius: 5rem;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 4rem;
    margin-bottom: 4rem;
  }
  @include m.tablet {
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 10rem;
    margin-bottom: 10rem;
    p {
      padding-left: 10rem;
      font-size: 2.4vw;
    }
    position: relative;
  }
  @include m.laptop {
    // margin-top: 16rem;
    margin-top: 12rem;
    margin-bottom: 15rem;
    // width: 50%;
    border-radius: 10rem;
    p {
      padding-left: 16rem;
      padding-top: 4rem;
      padding-bottom: 4rem;
      font-size: 2.7vw;
    }
  }
  // @include m.laptop {
  //   width: 65%;
  // }
  @include m.desktop {
    width: 50%;
    margin-bottom: 17rem;
    margin-left: 30%;
    margin-right: 25%;
  }
  @include m.desktopXL {
    // width: 50%;
    margin-top: 24rem;
    margin-bottom: 29rem;
    padding-left: 18rem;
    p {
      padding-left: 0;
      font-size: 3rem;
      padding-right: 5wv;
    }
  }
}
.directory-img {
  display: none;
  @include m.tablet {
    position: absolute;
    display: block;
    width: 15rem;
    bottom: -3.5rem;
    left: -2rem;
    img {
      width: 100%;
    }
  }
  @include m.laptop {
    width: 20rem;
    bottom: -18%;
    left: -5rem;
  }
  @include m.laptop {
    width: 25rem;
  }
  @include m.desktop {
    bottom: -2.5vw;
    left: -7rem;
  }
  @include m.desktopXL {
    width: 35rem;
    bottom: -8rem;
    left: -16rem;
  }
}

.home-squares {
  .squares-info {
    position: relative;
    background-color: v.$primaryPinkDark;
    display: grid;
    grid-template-rows: 1fr auto;
    margin: 2rem;
    .img-container {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        height: 10rem;
        width: 100%;
        object-fit: contain;
        padding: 1rem 2rem;
        @include m.mobile {
          width: 20rem;
        }
      }
    }
    .squares-text {
      padding: 0 3rem 3rem 3rem;
      text-align: center;
      color: v.$white;
    }
    @include m.mobile {
      margin: 3rem 15% 4rem 15%;
    }
    @include m.laptop {
      margin: 0;
      width: 45rem;
      padding: 5rem 2rem;
    }
    @include m.desktop {
      padding-right: 15rem;
      width: 55rem;
    }
  }
  .squares-photo {
    display: none;
    @include m.laptop {
      display: block;
      position: absolute;
      background-color: v.$primaryBeige;
      width: 50rem;
      height: 120%;
      right: -28rem;
      top: -10%;
      z-index: -1;
    }
    @include m.desktop {
      width: 60rem;
    }
  }
  .squares-photo-img {
    display: none;
    position: absolute;
    @include m.laptop {
      width: auto;
      height: 90%;
      right: -22.5rem;
      top: 5%;
      display: block;
      background-color: v.$white;
      img {
        width: 100%;
        height: 100%;
      }
      transition: all 0.5s;
      &:hover {
        transform: scale(1.025);
        transition: all 0.5s;
      }
    }
  }
  @include m.laptop {
    width: 73rem;
    margin: auto;
    margin-bottom: 10rem;
  }
  @include m.desktop {
    width: 80rem;
  }
}

.home-circles {
  display: none;
  @include m.tablet {
    display: block;
  }
  .circles-img {
    width: 30rem;
    margin: auto;
    margin-bottom: 3rem;
    img {
      width: 100%;
      height: 35rem;
    }
    @include m.desktop {
      width: 40rem;
      img {
        height: 45rem;
      }
    }
    @include m.desktopXL {
      margin-bottom: 10rem;
      width: 60rem;
      img {
        height: 65rem;
      }
    }
  }
  .circles-text {
    text-align: center;
    width: 22%;
    @include m.desktop {
      width: 20rem;
    }
  }
  h3 {
    color: v.$primaryPinkDark;
    font-family: v.$text;
    font-size: 1.3rem;
    @include m.desktop {
      font-size: 2rem;
    }
  }
  p {
    // font-family: v.$text;
    font-size: 1.3rem;
    @include m.desktop {
      font-size: 2rem;
    }
  }
  .circles-grid-2 {
    display: flex;
    justify-content: space-around;
    @include m.desktopXL {
      margin-left: 5rem;
      margin-right: 5rem;
    }
    @include m.desktopXXL {
      margin-left: 30rem;
      margin-right: 30rem;
    }
  }
  .circles-grid-1 {
    display: flex;
    justify-content: center;
  }
  .circles-text-up {
    margin-top: -12rem;
    @include m.laptop {
      margin-top: -7rem;
    }
    @include m.desktopXL {
      margin-top: -15rem;
    }
  }
  .circles-text-up-2 {
    margin-top: -17rem;
    @include m.laptop {
      margin-top: -25rem;
    }
    @include m.desktopXL {
      margin-top: -35rem;
    }
  }
  margin-bottom: 10rem;
  @include m.tablet {
    margin-bottom: 12rem;
  }
  @include m.laptop {
    margin-bottom: 20rem;
  }
}
