@use "base/variables" as v;
@use "base/mixins" as m;

.directory {
  h2 {
    font-family: v.$text;
    color: v.$primaryPinkDark;
    text-align: center;
    margin-top: 3rem;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
}

.lab-pack {
  .lab-pack-grid {
    display: grid;
    grid-template-columns: 1fr;
    .lab-pack-element-odd {
      border-radius: 3rem;
    }
    .lab-pack-element-even {
      border-radius: 3rem;
    }
    .lab-pack-element-odd,
    .lab-pack-element-even {
      position: relative;
      color: v.$secondaryGray;
      height: auto;
      background-color: v.$primaryBeige;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 2.5rem auto;
      width: 90%;
      @include m.mobile {
        width: 70%;
      }
      @include m.tablet {
        width: 85%;
      }
      @include m.desktopXL {
        width: 90%;
      }
    }
    .lab-pack-text {
      padding-top: 2rem;
      padding-bottom: 1rem;
      padding-left: 2rem;
      padding-right: 7rem;
      width: 100%;
      p {
        font-family: v.$text;
      }
    }
    .lab-pack-price {
      position: absolute;
      background-color: v.$primaryPinkDark;
      color: v.$white;
      width: 7rem;
      height: 7rem;
      right: -1rem;
      top: -2rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 5rem;
      @include m.laptop {
        top: -2.5rem;
        width: 8rem;
        height: 8rem;
      }
      p {
        margin: 0;
        font-size: 1.5rem;
        @include m.tablet {
          font-size: 2rem;
        }
        @include m.laptop {
          font-size: 2.5rem;
        }
      }
    }
    @include m.tablet {
      width: 70%;
      margin-left: auto;
      margin-right: auto;
    }
    @include m.laptop {
      grid-template-columns: repeat(2, 1fr);
      gap: 1rem;
      width: 100%;
    }
  }
  .lab-pack-final {
    .lab-pack-element-odd {
      border-radius: 3rem;
    }
    .lab-pack-element-even {
      border-radius: 3rem;
    }
    .lab-pack-element-odd,
    .lab-pack-element-even {
      position: relative;
      color: v.$secondaryGray;
      height: auto;
      background-color: v.$primaryBeige;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 2.5rem auto;
      width: 95%;
      @include m.mobile {
        width: 70%;
      }
      @include m.tablet {
        width: 45%;
      }
      @include m.desktopXL {
        width: 30%;
      }
      // @include m.laptop {
      //   width: 35%;
      // }
    }
    .lab-pack-text {
      padding-top: 2rem;
      padding-bottom: 1rem;
      padding-left: 2rem;
      padding-right: 7rem;
      width: 100%;
    }
    .lab-pack-price {
      position: absolute;
      background-color: v.$primaryPinkDark;
      color: v.$white;
      width: 7rem;
      height: 7rem;
      right: -1rem;
      top: -2rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 5rem;
      @include m.laptop {
        top: -2.5rem;
        width: 8rem;
        height: 8rem;
      }
      p {
        font-size: 1.5rem;
        margin: 0;
        @include m.tablet {
          font-size: 2rem;
        }
        @include m.laptop {
          font-size: 2.5rem;
        }
      }
    }
    margin-bottom: 7rem;
    @include m.tablet {
      margin-top: 5rem;
    }
  }
  margin: 1rem 1.5rem 3rem 1.5rem;
  @include m.mobile {
    margin: 3rem 1.5rem 3rem 1.5rem;
  }
  @include m.laptop {
    margin: 3rem 7rem 3rem 7rem;
  }
  @include m.desktopXL {
    margin: 5rem 25rem 3rem 25rem;
  }
}

.directory-packages {
  .element-img {
    display: none;
  }
  .package-element {
    position: relative;
    h3 {
      color: v.$primaryPinkDark;
      text-align: center;
      font-family: v.$title;
      span {
        font-family: v.$text;
      }
      @include m.laptop {
        font-size: 2.5rem;
      }
    }
    .element-text {
      background-color: v.$primaryPinkDark;
      color: v.$white;
      margin-top: 2rem;
      text-align: center;
      padding: 2rem;
      @include m.tablet {
        width: 80%;
        margin: 3rem auto;
      }
    }
    .element-price {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      bottom: -6rem;
      left: 0;
      width: 100%;
      p {
        background-color: v.$white;
        font-size: 1.5rem;
        color: v.$primaryPinkDark;
        border: 1px solid v.$primaryPinkDark;
        width: 6rem;
        height: 6rem;
        border-radius: 5rem;
        padding-top: 1.5rem;
        text-align: center;
        @include m.tablet {
          width: 8rem;
          height: 8rem;
          padding-top: 2.75rem;
          font-size: 1.75rem;
        }
        @include m.laptop {
          font-size: 2.5rem;
          width: 9.5rem;
          height: 9.5rem;
        }
      }
      @include m.tablet {
        bottom: -7rem;
      }
    }
    margin-bottom: 8rem;
  }
  @include m.laptop {
    .directory-element-odd {
      .element-text {
        width: 60%;
        margin-left: 10%;
        margin-right: 30%;
      }
      .element-price {
        right: 25%;
        bottom: -4rem;
        left: unset;
        width: unset;
      }
      .element-img {
        right: 15%;
      }
    }
    .directory-element-even {
      .element-text {
        width: 60%;
        margin-right: 10%;
        margin-left: 30%;
      }
      .element-price {
        left: 25%;
        bottom: -4rem;
        width: unset;
      }
      .element-img {
        left: 15%;
      }
    }
    .element-img {
      display: block;
      width: 15rem;
      position: absolute;
      bottom: 8rem;
      z-index: -1;
      img {
        width: 100%;
      }
    }
    .package-element {
      margin-top: 15rem;
    }
  }
  @include m.desktop {
    .directory-element-odd {
      .element-price {
        right: 27%;
      }
    }
    .directory-element-even {
      .element-price {
        left: 27%;
      }
    }
    .element-img {
      width: 22.5rem;
    }
    .package-element {
      margin-top: 20rem;
      .element-price {
        p {
          height: 10rem;
          padding-top: 3.5rem;
          width: 10rem;
        }
      }
    }
  }
  @include m.desktopXL {
    .directory-element-odd {
      h3 {
        margin-right: 40rem;
      }
      .element-text {
        width: 50%;
        margin-left: 15%;
        margin-right: 35%;
      }
      .element-price {
        right: 33%;
      }
      .element-img {
        right: 25%;
      }
    }
    .directory-element-even {
      h3 {
        margin-left: 40rem;
      }
      .element-text {
        width: 50%;
        margin-right: 15%;
        margin-left: 35%;
      }
      .element-price {
        left: 33%;
      }
      .element-img {
        left: 25%;
      }
    }
    .element-img {
      width: 25rem;
      bottom: 12rem;
    }
    .package-element {
      margin-top: 25rem;
    }
  }
  @include m.tablet {
    .reveal {
      position: relative;
      opacity: 0;
    }

    .reveal.active {
      opacity: 1;
    }
    // .active.fade-bottom {
    //   animation: fade-bottom 1s ease-in;
    // }
    .active.fade-left {
      animation: fade-left 1s ease-in;
    }
    .active.fade-right {
      animation: fade-right 1s ease-in;
    }
    // @keyframes fade-bottom {
    //   0% {
    //     transform: translateY(50px);
    //     opacity: 0;
    //   }
    //   100% {
    //     transform: translateY(0);
    //     opacity: 1;
    //   }
    // }
    @keyframes fade-left {
      0% {
        transform: translateX(-100px);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }

    @keyframes fade-right {
      0% {
        transform: translateX(100px);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
    .element-img {
      .reveal {
        position: relative;
        opacity: 0;
      }

      .reveal.active {
        opacity: 1;
      }
      // .active.fade-bottom {
      //   animation: fade-bottom 1s ease-in;
      // }
      .active.fade-left {
        animation: fade-left 1s ease-in;
      }
      .active.fade-right {
        animation: fade-right 1s ease-in;
      }
      // @keyframes fade-bottom {
      //   0% {
      //     transform: translateY(50px);
      //     opacity: 0;
      //   }
      //   100% {
      //     transform: translateY(0);
      //     opacity: 1;
      //   }
      // }
      @keyframes fade-left {
        0% {
          transform: translateX(-100px);
          opacity: 0;
        }
        100% {
          transform: translateX(0);
          opacity: 1;
        }
      }

      @keyframes fade-right {
        0% {
          transform: translateX(100px);
          opacity: 0;
        }
        100% {
          transform: translateX(0);
          opacity: 1;
        }
      }
    }
  }
}

.directory-btn {
  background-color: v.$primaryPinkDark;
  margin: 3rem 5rem;
  padding: 1rem 1rem;
  color: v.$white;
  font-size: 1.25rem;
  border-radius: 5rem;
  font-family: v.$text;
  &:hover {
    background-color: darken($color: v.$primaryPinkDark, $amount: 5);
    color: v.$white;
  }
  @include m.tablet {
    width: 15rem;
    margin-left: auto;
    margin-right: auto;
  }
  @include m.laptop {
    width: 20rem;
    font-size: 1.75rem;
  }
  @include m.desktop {
    width: 27rem;
    font-size: 2rem;
    margin-bottom: 10rem;
  }
  @include m.desktopXL {
    margin-top: 10rem;
  }
}
