@use "base/variables" as v;
@use "base/mixins" as m;

.background-image {
  position: fixed;
  z-index: -1;
  margin-top: 0rem;
  img {
    width: 100%;
  }
}

.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  @include m.mobile {
    justify-content: space-between;
  }
  // height: 5rem;
  height: auto;
  background-color: v.$primaryPink;
  background-color: v.$grayBackground;
  //   font-family: v.$textNarrow;
  font-family: v.$textNarrow;
  p {
    font-size: 1.35rem;
    margin: 0;
    margin-bottom: 0.25rem;
    color: v.$secondaryGray;
    font-family: v.$textNarrow;
    @include m.desktop {
      font-size: 1.75rem;
      padding-left: 1rem;
      margin-bottom: 0.5rem;
    }
  }
  a {
    height: 100%;
    img {
      height: 100%;
    }
  }
}

.header-phone {
  display: none;
  align-items: center;
  @include m.mobile {
    display: flex;
  }
  & > * {
    margin: 1rem;
  }
  .img-container {
    height: 2rem;
    padding-left: 1rem;
    @include m.laptop {
      padding-left: 3rem;
    }
    img {
      height: 100%;
    }
  }
}

.header-social-media {
  display: flex;
  align-items: center;
  @include m.tablet {
    padding-right: 1rem;
  }
  @include m.laptop {
    padding-right: 2rem;
  }
  .img-container {
    height: 2rem;
    margin: 1rem 1.5rem;
    @include m.laptop {
      // 
      margin: 3rem;
      margin-top: 1rem;
      margin-bottom: 1rem;
      // 
    }
    // &:last-child {
    //   padding-right: 1rem;
    //   @include m.laptop {
    //     padding-right: 2rem;
    //   }
    // }
    img {
      height: 100%;
    }
  }
  a {
    transition: transform .5s;
    display: flex;
    align-items: center;
    &:hover {
      transform-origin: center center;
      transform: scale(1.2);
      transition: transform .5s;
    }
    img {
      height: 100%;
    }
  }
}

.header-middle {
  height: 15rem;
  @include m.laptop {
    height: 20rem;
  }
  background-color: v.$primaryPinkDark;
  display: flex;
  align-items: center;
  justify-content: center;
  .img-container {
    height: 7.5rem;
    @include m.laptop {
      height: 12.5rem;
    }
    img {
      height: 100%;
    }
  }
}

.container-fluid {
  display: flex;
  justify-content: center;
}

.navbar {
  background-color: v.$white;
  color: v.$primaryPinkDark;
  font-family: v.$text;
  padding-bottom: 0;
  padding-top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  .container-fluid {
    padding: 0;
  }
  .navbar-toggler {
    margin: 1rem auto;
    padding: 0.75rem;
  }
}

@include m.laptop {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  height: auto;
  @include m.laptop {
    height: 7rem;
    width: 100%;
  }
  @include m.desktop {
    height: 10rem;
  }
  display: flex;
  align-items: center;
  .navbar {
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    @include m.laptop {
      flex-direction: row;
    }
    justify-content: space-evenly;
    align-items: center;
    .navbar-element {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      @include m.laptop {
        width: 20%;
        height: 7rem;
      }
      @include m.desktop {
        height: 10rem;
      }
      .img-container {
        height: 2rem;
        @include m.laptop {
          height: 2rem;
        }
        @include m.desktop {
          height: 3rem;
        }
        img {
          height: 100%;
        }
      }
      p {
        // font-family: v.$textNormal;
        // font-size: 1.25rem;
        margin: 1.35rem;
        font-size: 1.35rem;
        @include m.mobile {
          font-size: 1.5rem;
        }
        @include m.tablet {
          font-size: 1.6rem;
        }
        @include m.laptop {
          font-size: 2rem;
          margin: 1rem;
          margin-right: 0;
        }
        @include m.desktop {
          font-size: 2.1rem;
        }
        @include m.desktopXL {
          font-size: 2.2rem;
          margin: 2rem;
        }
      } 
    }
    a {
      text-decoration: none;
      //   color: v.$secondaryGray;
      &:hover {
        color: lighten($color: v.$primaryPinkDark, $amount: 15);
      }
    }
  }
}
